import './index.scss';
import React, { useRef } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';


export default function Sobre() {

    const navegacao = useNavigate();

    const handleScrollToSection = (sectionId) => {
        navegacao("/", { state: { sectionId } });

    };

    const especialidadesRef = useRef(null);
    const curiosidadeRef = useRef(null);
    const sobreRef = useRef(null);
    const homeRef = useRef(null);
    const location = useLocation();


    return (
        <div className="sobreMais">
            <div className="cabecalho">
                <img src="assets/image/logo.webp" alt="nao foi" />
                <div className="links">
                    <button className='button' onClick={() => handleScrollToSection('secao4')}>Home</button>
                    <button className='button' onClick={() => handleScrollToSection('secao3')}>Sobre Ns</button>
                    <button className='button' onClick={() => handleScrollToSection('secao1')}>Especialidades</button>
                    <button className='button' onClick={() => handleScrollToSection('secao2')}>Curiosidades</button>
                    <Link to="/">Contatos</Link>

                    <Link id='oloco' to='/telaLogin'> Login </Link>

                </div>
            </div>


            <div className="sobre">
                <div className="primeiro">
                    <h1>Sobre mim</h1>
                    <p>Me chamo Daniela Barbosa Rodrigues, tenho 36 anos, casada, me de 2 menina. Formada em Fisioterapia pela Universidade nove de Julho, POS Graduada em Fisioterapia Intensiva Peditrica e Neonatal pela FABIC. Formao em Pilates pela VOLL Pilates, Curso de Neurocincia da dor aplicada a prtica clinica pelo TUDO SOBRE DOR. Trabalhei nas instituies de sade da REDE DOR das unidades do Jabaquara e Morumbi. Atuando tanto em pediatria e adulto. Realizo atendimento domiciliar na regio  da Zona Sul de So Paulo.</p>
                </div>

                <div className='grup'>
                    <div className="missao">
                        <h1>Viso</h1>
                        <p>"Ser referncia em atendimento fisioteraputico domiciliar na Zona Sul de So Paulo, promovendo a sade e o bem-estar de crianas e adultos com excelncia e humanizao."</p>
                    </div>

                    <div className="missao">
                        <h1>Misso</h1>
                        <p>"Oferecer servios de fisioterapia de alta qualidade, com foco em cuidados peditricos e adultos, utilizando abordagens integrativas e personalizadas, sempre priorizando a sade e o conforto dos nossos pacientes."</p>
                    </div>


                </div>

                                
                <h1 className='titulo'> Valores </h1>

                <div className="valores">

                    <div className='mae'>
                        <h3 className='topicos'>Excelncia:</h3>
                        <p>Compromisso com a qualidade no atendimento e resultados clnicos.</p>
                    </div>
                    <div className='mae'>
                        <h3 className='topicos'>Humanizao:</h3>
                        <p>Atendimento acolhedor e emptico, respeitando as necessidades de cada paciente.</p>

                    </div>
                    <div className='mae'>
                        <h3 className='topicos'>Inovao:</h3>
                        <p> Atualizao constante em tcnicas e conhecimentos, integrando as melhores prticas na fisioterapia.</p>

                    </div>
                    <div className='mae'>
                        <h3 className='topicos'>Responsabilidade:</h3>
                        <p>Compromisso com a sade e segurana dos pacientes e suas famlias.</p>
                    </div>
                    <div className='mae'>
                        <h3 className='topicos'>Empatia:</h3>
                        <p>Compreenso e sensibilidade nas interaes com pacientes e colaboradores.</p>

                    </div>
                </div>

            </div>


            <div className="rodape">
                <div className="um">
                    <img id='logo' src="/assets/image/logo1.png" alt="logoRodape" />
                    <div className="redes">
                        <img id='a' src="/assets/image/linkedin.svg" alt="" />
                        <img id='b' src="/assets/image/instagram.svg" alt="" />
                        <img id='c' src="/assets/image/twitter-x.svg" alt="" />
                    </div>
                </div>

                <div className="dois">
                    <h1>HSSW Code.</h1>
                    <h1>O que persiste  o aprendizado.</h1>
                </div>

                <div className="tres">
                    <h1>Explore</h1>
                    <div className="links">
                        <button className='final' onClick={() => homeRef.current.scrollIntoView({ behavior: 'smooth' })}>Home</button>
                        <button className='final' onClick={() => sobreRef.current.scrollIntoView({ behavior: 'smooth' })}>Sobre Ns</button>
                        <button className='final' onClick={() => especialidadesRef.current.scrollIntoView({ behavior: 'smooth' })}>Especialidades</button>
                        <button className='final' onClick={() => curiosidadeRef.current.scrollIntoView({ behavior: 'smooth' })}>Curiosidades</button>
                        <Link to="/">Contatos</Link>
                    </div>
                </div>

                <div className="quatro">
                    <h1>Politica de privacidade</h1>
                    <h1>FAQ</h1>
                </div>
            </div>
        </div>

    );
}
